	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Stutter Group","date":"21 Sep 2006","slug":"/2006-09-21-stutter-group/","tags":"Y2006"},"html":"<p>After having had the summer off, Christopher had his first speech therapy session today. A clinic in Hamburg has started a stutter group for children aged 6 to 8 with 2 therapists and 9 children that will meet for 90 minutes every Thursday. The therapy has a large social component, simply bringing them together so they can experience first hand that *other kids stutter too*. That will be a new experience for Christopher, even in the special speech kindergarten he was the only stutterer. The experience is important for us parents as well... *other parents have children who stutter*.</p>\n<p>The group seems to have gotten off to a good start. I didn't wait around the whole 90 minutes, but when I came back 15 minutes before the end the room was quite loud. They were having a pillow fight, and after the session Christopher and 4 of the boys were happy to start an extra round. The group is 8 boys and 1 girl... I hope the girl doesn't get scared off. Two or three of the boys are Turkish... that's good, since his school class is rather homogeneous.</p>\n<p>Reminder to self: I need to make an appointment with the pediatrician to get a prescription/referral for the group sessions, so that health insurance will cover the costs.</p>"},"next":{"fields":{"title":"Baltic Whackjobs","date":"18 Sep 2006","slug":"/2006-09-18-baltic-whackjobs/"}},"previous":{"fields":{"title":"Trash your computer","date":"21 Sep 2006","slug":"/2006-09-21-trash-your-computer/"}}},{"node":{"fields":{"title":"Baltic Whackjobs","date":"18 Sep 2006","slug":"/2006-09-18-baltic-whackjobs/","tags":"Y2006"},"html":"<p>In elections yesterday in Mecklenburg-Vorpommern, the poorest German state, the far-right National Democratic Party <a href=\"http://news.bbc.co.uk/2/hi/europe/5349696.stm\">earned 7.3% of the vote</a>, enough to enter parliament. As always when the far-right does well in an election, there is much wailing and gnashing of teeth in the German media. Doug Merrill at A Fistful of Euros is <a href=\"http://fistfulofeuros.net/archives/002683.php\" title=\"Sandy Brown Baltic Shores\">not quite so hysterical</a>:</p>\n<blockquote><p>\n  On the whole, though, Id be cautiously optimistic... in a state with 18 percent unemployment, the extremist vote was below 10 percent. ... the far right has ebbed and flowed throughout the postwar period, and its never gotten anywhere close to a share of power at the national level... (finally) the far right tends to fractiousness, as the disintegration of the NPD parliamentary party in Saxony in late 2005 most recently showed.</p>\n<p>  One in 14 voters in Mecklenburg-Vorpommern is willing to cast a ballot for right-wing whackjobs. That, for better or for worse, is probably not too far from the European norm.\n</p></blockquote>\n<p>Neo-Nazis aren't interested in politics, they just like the sound of their own voice. I'd be more worried about the low turnout (59.2%), which means a good share of the voters aren't interested in politics either.</p>"},"next":{"fields":{"title":"How to keep your cell phone from disrupting family time","date":"16 Sep 2006","slug":"/2006-09-16-how-to-keep-your-cell-phone-from-disrupting-family-time/"}},"previous":{"fields":{"title":"Stutter Group","date":"21 Sep 2006","slug":"/2006-09-21-stutter-group/"}}},{"node":{"fields":{"title":"How to keep your cell phone from disrupting family time","date":"16 Sep 2006","slug":"/2006-09-16-how-to-keep-your-cell-phone-from-disrupting-family-time/","tags":"Y2006"},"html":"<p>On Balance, the Washington Post blog on \"Juggling Work and Family\" <a href=\"http://blog.washingtonpost.com/onbalance/2006/09/five_tricks_to_tame_technology.htmlOn\">Five Tricks to Tame Technology</a>:</p>\n<blockquote><p>\n  Don't like your Treo buzzing at dinner? The solution is pretty simple. Shut it off. Or throw it into the Potomac. You own the device, not the other way around.\n</p></blockquote>\n<p>(via <a href=\"http://www.parenthacks.com/2006/09/how_to_keep_you.html\">parent hacks</a>)</p>"},"next":{"fields":{"title":"Gammelgemuse","date":"16 Sep 2006","slug":"/2006-09-16-gammelgemuse/"}},"previous":{"fields":{"title":"Baltic Whackjobs","date":"18 Sep 2006","slug":"/2006-09-18-baltic-whackjobs/"}}}],"pathPrefix":"","first":false,"last":false,"index":190,"pageCount":1101}}

